        .CodingHelper_header {
            background-color: #35208c;
            color: white;
            padding: 5px;
            text-align: center;
        }

        .CodingHelper_header h1 {
            color: #ffffff;
            margin: 10px;
            border: 0px;
            padding: 0px;
            text-align: center;
        }

        .CodingHelper_body {
            font-family: 'Noto Sans KR', sans-serif;
            line-height: 1.6;
            margin: 0;
            padding: 0;
            background-color: #f5f5f5;
            color: #333;
        }

        .CodingHelper_container {
            max-width: 1200px;
            margin: 10px auto;
            background-color: white;
            padding: 30px;
            border-radius: 10px;
            box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
        }

        .CodingHelper_body .version-container {
            display: flex;
            justify-content: space-between;
            gap: 20px;
            margin-bottom: 40px;
        }

        .CodingHelper_body .version-box {
            flex: 1;
            background-color: white;
            padding: 20px;
            border-radius: 8px;
            box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
            text-align: center;
            transition: transform 0.3s ease;
            display: flex;
            flex-direction: column;
            align-items: center;
        }

        .CodingHelper_body .version-box:hover {
            transform: translateY(-5px);
        }

        .CodingHelper_body .version-title {
            background-color: #35208c;
            color: white;
            padding: 15px;
            border-radius: 8px 8px 0 0;
            margin: -20px -20px 20px -20px;
            font-size: 1.2em;
            font-weight: bold;
            width: 100%;
        }

        .CodingHelper_body img.version-img {
            width: 100%;
            max-width: 300px;
            height: auto;
            border-radius: 0px;
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
            margin: 15px 0;
        }

        .CodingHelper_body img.manual-img {
            width: 100%;
            max-width: 500px;
            height: auto;
            border-radius: 8px;
            box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
            margin: 0px 0px 10px 0px;
        }

        .CodingHelper_body a {
            display: inline-block;
            margin: 20px 0;
            padding: 12px 25px;
            background-color: #35208c;
            color: white;
            text-decoration: none;
            border-radius: 5px;
            transition: all 0.3s ease;
            font-weight: 500;
            width: 80%;
            max-width: 200px;
        }

        .CodingHelper_body a:hover {
            background-color: #1a252f;
            transform: translateY(-2px);
            box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
        }

        .CodingHelper_body ol {
            padding-left: 20px;
            margin-bottom: 30px;
        }

        .CodingHelper_body li {
            margin-bottom: 20px;
            list-style-type: none;
        }

        .CodingHelper_body hr {
            margin: 40px 0;
            border: none;
            border-top: 2px solid #e0e0e0;
        }

        .CodingHelper_body .warning {
            background-color: #fff3cd;
            color: #856404;
            padding: 15px;
            border-radius: 5px;
            margin: 15px 0;
            border-left: 4px solid #ffc107;
        }

        .CodingHelper_body .information {
            background-color: #e8f4fc;
            color: #0c5460;
            padding: 15px;
            border-radius: 5px;
            margin: 15px 0;
            border-left: 4px solid #8ac5ec;
            line-height: 1.8;
        }

        @media (max-width: 768px) {
            .CodingHelper_body .version-container {
                flex-direction: column;
            }

            .CodingHelper_body .CodingHelper_container {
                padding: 15px;
            }

            .CodingHelper_body h1 {
                font-size: 2em;
            }
        }